<template>
	<div class="payType padding8 marginTop-14 backgroundColor-fff font-24">
		<div class="borderButton-e5e5e5 padding18">
			<payType class="Alipay"><Checkbox v-model="single" @click.native="choose('Alipay')"></Checkbox></payType>
			<payType class="WeChat"><Checkbox v-model="single2" @click.native="choose('WeChat')"></Checkbox></payType>
			<payType class="baidu"><Checkbox v-model="single3" @click.native="choose('baidu')"></Checkbox></payType>
			<!-- <RadioGroup v-model="payChoose">
				<payType class="Alipay"><Radio name="Alipay" label="Alipay"></Radio></payType>
				<payType class="WeChat"><Radio name="WeChat" label="WeChat"></Radio></payType>
				<payType class="baidu"><Radio name="baidu" label="baidu"></Radio></payType>
			</RadioGroup> -->
		</div>
	</div>
</template>
<script>
export default({
data:function(){
	return {
		single: false,
		single2:false,
		single3:false,
		payChoose:''
	}
},
methods:{
	choose:function(type){
		this.single=false;
		this.single2=false;
		this.single3=false;
	}
},
components:{
	"payType":{
			template:'<div class="overflow payTypeBox"><span class="PayIcon left"></span><span class="right"><slot></slot></span></div>'
		}
}
})	
</script>
<style lang="less" scoped>
.payType{
	.payTypeBox{
		line-height:0.533333rem;
	}
	.payTypeBox:not(:last-child){
		padding-bottom:0.333333rem
	}
	/deep/.PayIcon{
		width:0.533333rem;
		height:0.533333rem;
		
	}
	/deep/.Alipay .PayIcon{
		background:url(../../assets/Alipay.png) no-repeat center center;
		background-size: cover;
	}
	/deep/.WeChat .PayIcon{
		background:url(../../assets/WeChat.png) no-repeat center center;
		background-size: cover;
	}
	/deep/.baidu .PayIcon{
		background:url(../../assets/baidu.png) no-repeat center center;
		background-size: cover;
	}
}
</style>


