// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import Iview from 'iview'
import store from './store'

import 'iview/dist/styles/iview.css'
import './css/common.css'



Vue.use(Iview);

Vue.prototype.$http=axios;
Vue.config.productionTip = false

var VueTouch = require('vue-touch')
Vue.use(VueTouch, {name: 'v-touch'})
Vue.prototype.HOST='/api'
/* eslint-disable no-new */
/*new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})*/
Vue.component('topBar',{
	props:['name'],
	template:'<div class="topBar flexBetween font-36"><span @click="$router.back(-1)" class="icon font-50"><Icon type="ios-arrow-back" class="back"></Icon></span><span class="name">{{name}}</span><span class="icon font-50"><Icon type="ios-more" class="href"></Icon></span></div>'
})

Vue.component('topBarIndex',{
	props:['name'],
	template:'<div class="topBar flexBetween font-36"><span class="icon font-50"></span><span class="name">{{name}}</span><span class="icon font-50"><Icon type="ios-more" class="href"></Icon></span></div>'
})

Vue.component('space',{
	template:'<div class="space"></div>'
})

Vue.filter('change',function(value){
		return value?'无货':'失效';
})

const app = new Vue({
	router: router,
	store:store,
	render: h => h(App),
}).$mount('#app')


