<template>
	<div id="Addaddress">
		<topBar name="新增地址"></topBar>
		<div class="am-list font-28 padding8 backgroundColor-fff">
			<div class="name borderButton-e5e5e5">
				<input type="text" name="name" placeholder="收货人姓名" v-model="name">
			</div>
			<div class="phone borderButton-e5e5e5">
				<input type="text" name="phone" placeholder="手机号码" v-model="phone">
			</div>
			<div class="city borderButton-e5e5e5">
				<select name="" id="city" v-model="cityNO">
					<option value="0">请选择城市</option>
					<option value="1">上海</option>
					<option value="2">北京</option>
					<option value="3">江苏</option>
					<option value="4">广州</option>
					<option value="5">深圳</option>
				</select>
			</div>
			<div class="address borderButton-e5e5e5">
				<select name="" id="qu" v-model="quNO">
					<option value="0">请选择市/区</option>
					<option :value="item.no" v-for="(item , index) in qu">{{item.name}}</option>
				</select>
			</div>
			<div class="addressInfo borderButton-e5e5e5">
				<input type="text" name="addressInfo" placeholder="详细地址" v-model="addressInfo">
			</div>
		</div>
		<div class="font-24 padding8 backgroundColor-fff marginTop-20 single">
			<Checkbox v-model="single" class="font-24">设置为默认地址</Checkbox>
		</div>
		<div class="padding8 marginTop-45">
			<button class="save font-32 button2">保存并使用</button>
		</div>
		
	</div>
</template>
<script>
export default({
	data:function(){
		return {
			city:'',
			qu:'',
			single:false,
			id:this.$route.params.id,
			address:'',
			name:'',
			phone:'',
			cityNO:'0',
			quNO:'0',
			addressInfo:'',


		}
	},
	created:function(){
		let that=this;
		this.$http({
			url:'./static/falseDate/qu.json',
			method:'get'
		}).then(function(data){
			that.qu=data.data.data;
		}).catch(function(data){
			console.log(22)
		})
		if(this.id){
			this.$http({
				url:'./static/falseDate/editAddress.json',
				method:'get'
			}).then(function(data){
				data.data.data.forEach(function(item,index){
					if(item.id==that.id){
						that.address=data.data.data[index];
						that.name=that.address.name;
						that.phone=that.address.phone;
						that.cityNO=that.address.cityNO;
						that.quNO=that.address.quNO;
						that.addressInfo=that.address.address;
					}
				})
			})
		}
	}
})
</script>
<style lang="less" scoped>
#Addaddress{background-color:#f6f6f6}
.am-list{
	input,select{
		width:100%;
		height:1.12rem;
		border: 0;
		outline: none;
		background-color:transparent;
		 border-color:transparent;	 	
		-webkit-appearance: none;
	}
}
.single{
	height:2.4rem;
	line-height:1.266667rem;
	/deep/.ivu-checkbox-inner{
		width:0.32rem;
		height:0.32rem;
	}
	/deep/ .ivu-checkbox-checked .ivu-checkbox-inner:after{
		width:0.133333rem;
		height:0.24rem
	}
}
.save{
	width:100%;
	height:1.266667rem;
}
</style>


