<template>
<div id="address">
	<topBar name="收获地址"></topBar>
	<div class="font-24  addressList">
	
		<div class="address padding8 padding38 backgroundColor-fff" v-for="(item , index) in addressList">
			<div class="overflow">
				<span class="left">{{item.name}}</span>
				<span class="right">{{item.phone}}</span>
			</div>
			<div class="color-888888 borderButton-e5e5e5 addressInfo">
				<span class="">{{item.address}}</span>
			</div>
			<div class="color-888888 overflow marginTop-40">
				<Checkbox  v-model="flag[index]" ref="check" class="left font-24" @change.native="check(index)">默认地址</Checkbox>
				<span class="right">
					<Icon type="ios-compose-outline" class="font-32" @click.native="edit(item.id)"></Icon><span class="marginLeft-10">编辑</span>
					<Icon type="ios-trash-outline" class="font-32 marginLeft-30" @click.native="delet(index)"></Icon><span class="marginLeft-10">删除</span>
				</span>

			</div>
		</div>
	</div>
	<space></space>
	<router-link to="/addAddress">
		<div class="addAddress">
			<button class="button3 color-ff0000 font-32"><Icon type="ios-plus-outline"></Icon>添加新地址</button>
		</div>
	</router-link>
</div>
</template>
<script>
export default({
	data:function(){
		return{
			addressList:'',
			flag:[]
		}
	},
	created:function(){
		let that=this;
		this.$http({
			url:'./static/falseDate/addressList.json',
			method:'get'
		}).then(function(data){
			let i=0;
			that.addressList=data.data.data
			for(i in that.addressList){
				that.flag[i]=that.addressList[i].single;
			}
		}).catch(function(data){
			console.log(2)
		})
	},
	methods:{
		edit:function(id){
			this.$router.push({name:'addAddress',params:{id:id}})
		},
		delet:function(index){
			this.addressList.splice(index,1);
		},
		check:function(index){
			for(let i=0;i<this.flag.length;i++){
				if(i!=index){
					this.$set(this.flag,i,false)
				}
				
			}
		}
	}
})
</script>
<style lang="less" scoped>
#address{background-color:#f6f6f6}
.addressList{
	.address:not(:last-child){
		margin-bottom:0.266667rem;
	}
	.addressInfo{padding-bottom:0.266667rem;}
	/deep/.ivu-checkbox-inner{
		width:0.32rem;
		height:0.32rem
	}
	/deep/ .ivu-checkbox-checked .ivu-checkbox-inner:after{
		width:0.133333rem;
		height:0.24rem
	}
}
.addAddress{
	width:100%;
	position:fixed;
	bottom:0;
	border-top:1px solid #e5e5e5
}
</style>


