<template>
	<div id="pay">
		<topBar name="付款页面"></topBar>
		<addressBox></addressBox>
		<info></info>
		<div class="choose padding8 marginTop-14 backgroundColor-fff font-24 ">
			<div class="borderButton-e5e5e5 padding38">
				<span class="marginRight-60">配送方式</span>
				<!-- <Checkbox v-model="take" class="font-24 check">自取</Checkbox>
				<Checkbox v-model="package" class="font-24 check">快递(8元)</Checkbox> -->
				 <RadioGroup v-model="picked">
					 <Radio label="take" class="font-24 check" size="large">自取</Radio>
					 <Radio label="package" class="font-24 check" size="large">快递(8元)</Radio>
				 </RadioGroup>

			</div>
			<div class="chooseTime padding38">
				<span class="marginRight-60">配送时间</span>
				<span>工作日、双休日与假日均可配送</span>
				<Icon type="ios-calendar-outline" class="font-32 time"></Icon>
				<select name="psTime" class="psTime">
					<option value="0">工作日、双休日与假日均可配送</option>
					<option value="1">工作日配送</option>
					<option value="2">双休日配送</option>
					<option value="3">假日配送</option>
				</select>
			</div>
		</div>
		<div class="quan padding8 marginTop-14 backgroundColor-fff font-24 padding38 flexBetween">
			<span>电子券</span>
			<router-link to="/card">
				<span class="flex">
					<span class="color-dd2727">¥{{money}}</span>
					<Icon type="ios-arrow-forward" class="font-30 marginLeft-10"></Icon>
				</span>
			</router-link>
		</div>
		<space></space>
		<div class="footer backgroundColor-fff borderTop-e5e5e5 font-24 flexBetween">
			<span>合计：<span class="color-dd2727">¥{{total}}</span></span>
			<router-link to="/payType">
				<div>
					<a class="width gm font-30">立即支付</a>
				</div>
			</router-link>
		</div>
	</div>
</template>
<script>
import addressBox from './addressBox.vue'
import info from './info.vue'
export default({
	data:function () {
		return{
			picked:'',
			money:10,
			total:74.80
		}
	},
	created:function(){
		let cardMoney=this.$route.params.money;
		if(cardMoney){
			this.money=cardMoney
		}
	},
	methods:{
	},
	components:{
		addressBox,
		info
	}
})
</script>
<style lang="less" scoped>
#pay{background-color:#f6f6f6}
a{color:#000}

.choose{
	.check{
	/deep/	.ivu-radio-inner:after{
			width: 0.253333rem;
			height:0.253333rem;
			border-radius: 50%;
		}
	/deep/	.ivu-radio-inner{
			width:0.32rem;
			height:0.32rem;
		}
	}
	.chooseTime{
		.time{
			position: absolute;
	    	right: 8px;
		}
		.psTime{
			opacity: 0;
    		position: absolute;
    		right:0
		}
	}

}
.footer{
	overflow:hidden;
	position: fixed;
    bottom: 0;
    width: 100%;
    padding-left:8px;
	.width{
		width:2.933333rem;
		height:1.306667rem;
		line-height:1.306667rem;
		display:block;
    	display: inline-block;
    	text-align:center;
    	color:#fff;
	}
	.gm{
		background-color:#dd2727
	}
}
</style>


