<template>
	<div id="cardInfo">
		<topBar name="电子券详细页"></topBar>
		<div class="margin8 marginTop-8 border-e4e4e4 borderR6">
			<div class="card  padding14" :class="color">
				<div class="money font-72">
					<span class="font-28">￥</span>{{cardInfo.money}}
				</div>
				<div class="name font-30">{{cardInfo.name}}</div>
				<div class="rule font-30">{{cardInfo.rule}}</div>
				<div class="cq">
					<span class="font0 font-20">卡券类型</span>
					<span>{{cardInfo.type}}</span>
				</div>
				<div class="cq">
					<span class="font0 font-20">有效期至</span>
					<span>{{cardInfo.time}}</span>
				</div>
			</div>
			<div class="content">
				<h2 class="title">优惠券发行单位名称</h2>
				<div class="padding36">
					<p class="font-29 blod">活动内容：</p>
					<p class="font-26 blod">莫扎特最脍炙人口的名作的现场演奏，感受和了解莫扎特对中提琴这件弦乐器丰富而独特表现力的出色运用</p>
				</div>
				<div class="padding46">
					<p class="font-29 blod">使用说明：</p>
					<p class="font-26 blod">VIP专享会员，截止目前累计积分超1000的会员用户可使用</p>
				</div>
				<div class="align">
					<button class="font-32 button4 blod" @click.prevent="choose(cardInfo.money)">立即使用</button>
				</div>
				<p class="title font-26 footer">到店出示卡券，由服务顾问引导使用</p>
			</div>
		</div>
	</div>
</template>
<script>
export default({
	data:function(){
		return{
			cardInfo:'',
			card:'',
			color:''
		}
	},
	created:function(){
		let cardId=this.$route.params.id;
		this.color=this.$route.params.color;
		let that=this;
		this.$http({
			url:'./static/falseDate/card.json',
			method:'get'
		}).then(function(data){
			that.card=data.data.data;
			
			for (let i in that.card){
				if(that.card[i].id==cardId){
					that.cardInfo=that.card[i];
					break;
				}
				
			}
		})
	},
	methods:{
		choose:function(money){
			this.$router.push({name:'pay',params:{money:money}})
		}
	}
})
</script>
<style lang="less" scoped>

.red{background-color:#fe5a59}
.green{background-color:#20d391}
.yello{background-color:#fec11b}
.borderR6{border-radius:6px}
.card{
	border-bottom: 4px dashed;
	border-radius: 6px;color:#fff;
	padding-left:0.32rem;

	.money{
		//font-size:3.6rem;
	}
	.rule{
		font-weight: bold;
	}
	.font0{color:#000;opacity:0.56}
	.cq{
		display:inline-block;
		margin-right:1.333333rem;
		margin-top:0.666667rem;
		span{
			display:block;
		}
	}
}
.content{
	padding-top:0.746667rem;
	background-color:#fff;
	color:#444444;
	padding-left:0.533333rem;
	padding-right:0.533333rem;
	.title{
		text-align:center;
		color:#b7b7b7
	}
	.padding36{
		padding-top:0.48rem;
	}
	.padding46{
		padding-top:0.613333rem;
	}
	.footer{
		line-height:1.12rem;
	}
}
</style>


