<template>
	<div>
		<!-- <router-link to="/address"> -->
		<a @click="router">
			<div class="padding8 font-24 padding46 backgroundColor-fff addDress flex">
				<div v-if="address==''" class="addressText">
					<span>添加默认地址</span><Icon type="ios-plus-outline" class="marginLeft-12 font-30"></Icon>
				</div>
				<div v-else class="addressText">
					<div>
						<span>收货人：{{address.name}}</span>
						<span>{{address.phone}}</span>
					</div>
					<div>
						<Icon type="ios-location-outline"></Icon>
						<span>收货地址：{{address.address}}</span>
					</div>
				</div>
				<div class="addressMore">
					<Icon type="ios-arrow-forward" class="right font-30 color-c6c6c6"></Icon>
				</div>
			</div>
		<!-- </router-link> -->
		</a>
	</div>
</template>
<script>
export default({
	props:['attr'],
	data:function(){
		return{
			address:''
		}
	},
	created:function(){
		let that=this;
		this.$http({
			url:'./static/falseDate/address.json',
			method:'get'
		}).then(function(data){
			that.address=data.data;
		}).catch(function(data){
			console.log(2)
		});
	},
	methods:{
		router:function(){
			if(this.attr!='disabled')
				this.$router.push({path:"/address"})
		}
	}
})
</script>
<style lang="less" scoped>
a{color:#000}
.addDress{
	.addressText{
		display:inline-block;
		width:95.5%
	}
	.addressMore{
		display:inline-block;
	}
}
</style>


