<template>
	<div id="card">
		<topBar name="电子券"></topBar>
		<div class="padding8">
			<card v-for="(item,index) in card" :money="item.money" :name="item.name" :rule="item.rule" :type="item.type" :time="item.time" :class="background[index]" @click.native="choose(item.id,background[index])"></card>
		</div>
		
	</div>
</template>
<script>
export default({
data:function(){
	return{
		card:'',
		background:['red','green','yello']
	}
},
created:function(){
	let that = this;
	this.$http({
		url:'./static/falseDate/card.json',
		method:'get'
	}).then(function(data){
		that.card=data.data.data
	}).catch(function(data){
		console.log(2)
	})
},
methods:{
	choose:function(id,color){
		//this.$router.push({name:'pay',params:{money:money}})
		
		this.$router.push({name:'cardInfo',params:{id:id,color:color}})
	}
},
components:{
	"card":{
		props:['money','name','rule','type','time'],
		template:'<div class="card marginTop-8 padding14"><div class="masker top"></div><div class="money font-72"><span class="font-28">￥</span>{{money}}</div><div class="name font-30">{{name}}</div><div class="rule font-30">{{rule}}</div><div class="cq"><span class="font0 font-20">卡券类型</span><span>{{type}}</span></div><div class="cq"><span class="font0 font-20">有效期至</span><span>{{time}}</span></div><div class="masker bottom"></div></div>'
	}
}
})
</script>
<style lang="less" scoped>

.red{background-color:#fe5a59}
.green{background-color:#20d391}
.yello{background-color:#fec11b}
.card{
	border-radius: 6px;color:#fff;
	padding-left:0.32rem;
	position:relative;
	/deep/.money{
		//font-size:3.6rem;
	}
	/deep/.rule{
		font-weight: bold;
	}
	/deep/.font0{color:#000;opacity:0.56}
	/deep/.cq{
		display:inline-block;
		margin-right:1.333333rem;
		margin-top:0.666667rem;
		span{
			display:block;
		}
	}
	/deep/.masker{
		background-color:#f6f6f6;
		width:0.266667rem;
		height:0.533333rem;
		border-radius: 0.266667rem;
		position: absolute;
    	right: 3.28rem;
	}
	/deep/.bottom{
		bottom: -0.32rem;
	}
	/deep/.top{
		top: -0.32rem;
	}

}
</style>


