<template>
<div id="index">
    <topBarIndex name="商城"></topBarIndex>
    <div class="index-cx">
      <img :src="index_cx"> 
    </div>
    <ul class="cxPic">
        <li class='col' v-for="(item, index) in mainpic" @click="cx(item.url)">
            <a class='colpic' :class="icons[index]"></a>
            <span class="colpic_name font-24">{{item.name}}</span>
        </li>
    </ul>
    <div class="newGG">
        <span class="gg_left">最新公告</span>
        <div class="gg_right">
            <span class="laba"></span>
            <span class="ldgg font-20">
                <p>江苏大剧院艺术节纪念品现已上线</p>
                <label>优惠</label><span class="color-c08f28">新用户下单立享9折</span>
            </span>
        </div>
    </div>
    <FL name='热门推荐'></FL>
    <Row :gutter="16" style="margin-left: 8px;margin-right: 8px;">
        <Col span="8" v-for="(item,index) in rmtj">
            <div class="border-e5e5e5 rmtjList">
                <span class="font-24">{{item.name1}}</span>
                <span class="color-e49a00 font-24">{{item.name2}}</span>
                <img :src="item.img">
            </div>
        </Col>
    </Row>
    <FL name='商品兑换'><router-link to="/more" class="more_right font-40"><Icon type="ios-arrow-forward" ></Icon></router-link></FL>
    <Row :gutter="16" style="margin-left: 8px;margin-right: 8px;">
      <Col span="12" v-for="(item , index) in spdh" class="spdh_box">
          <div class="border-e5e5e5 rmtjList spdh">
              <img :src="flag_re" v-if="item.flag=='re'" class="flag">
              <img :src="flag_new" v-if="item.flag=='new'" class="flag">
              <img :src="item.img">
          </div>
          <span class="font-28 line-height-60">{{item.name}}</span>
          <span class="font-24 color-e49a00">{{item.jif}}</span>
      </Col>
    </Row>
    <FL name='更多商品'><router-link to="/more" class="more_right font-40"><Icon type="ios-arrow-forward" ></Icon></router-link></FL>
    <Row :gutter="16" style="margin-left: 8px;margin-right: 8px;">
      <Col span="12" v-for="(item , index) in more" class="spdh_box">
          <div class="border-e5e5e5 rmtjList spdh">
              <img :src="flag_re" v-if="item.flag=='re'" class="flag">
              <img :src="flag_new" v-if="item.flag=='new'" class="flag">
              <img :src="item.img">
          </div>
          <span class="font-28 line-height-60">{{item.name}}</span>
          <span class="font-24 color-e49a00">{{item.jif}}</span>
      </Col>
    </Row>
    <div class="bottom">
        <span class="bottom_line"></span>
        <img :src="bottom">
        <span class="bottom_line" style="transform: rotate(180deg)"></span>
        <div class="font-22 line-height-60">没有更多了~</div>
    </div>
</div>
</template>

<script>
export default{
  data:function(){
    return{
      index_cx:'./static/images/index-cx.png',
      flag_re:'./static/images/rmsp/re.png',
      flag_new:'./static/images/rmsp/new.png',
      bottom:'./static/images/bottom.png',
      colpic:'colpic',
      mainpic:'',
      rmtj:'',
      spdh:'',
      more:'',
      icons:['jnp','hhys','wdys','jyzb','gwc']
    }
  },
  mounted:function(){
    let that=this;
      this.$http({
        url:'./static/falseDate/mainpic.json',
        method:'get',
        data:'',
      }).then(function(data){
        that.mainpic=data.data.data;
      }).catch(function(data){
        console.log(2)
      });

      this.$http({
        url:'./static/falseDate/rmtj.json',
        method:'get',
        data:''
      }).then(function(data){
        that.rmtj=data.data.data;
      }).catch(function(data){
        console.log(2)
      });

      this.$http({
        url:'./static/falseDate/spdh.json',
        method:'get',
        data:''
      }).then(function(data){
        that.spdh=data.data.data;
        that.more=data.data.more;
      }).catch(function(data){
        console.log(2)
      });

       this.$http({
       // url:this.HOST+'/demo01/tests.do',
       url:this.HOST+'/demo01/tests.do',
        method:'get',
        data:''
      }).then(function(data){
        console.log(data)
      }).catch(function(data){
        console.log(2)
      });

      /*this.$http({
        url:'tests.do',
        method:'get',
        data:''
      }).then(function(data){
        console.log(data)
      }.catch(function(data){
        console.log(2)
      })*/
  },
  methods:{
    cx:function(url){
      this.$router.push({path:url})
    }
  },
  components:{
    "ColY":{
      template:"<li class='col'><slot></slot></li>"
    },
    "FL":{
      props:['name'],
      template:"<div class='rmtj color-666666 flexBetween font-28'>{{name}}<slot></slot></div>"
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
#index{background-color:#fff}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
  letter-spacing: -8px;
}
li {
  display: inline-block;
  margin: 0 10px;
  letter-spacing: normal;
}
a {
  color: #42b983;
}
.index-cx,.index-cx img{width: 100%}

.cxPic{
  margin:0.506667rem 0;
    .col{
      width: 20%;
      margin:0;
      text-align: center;
    }
    .colpic_name{
      display:inline-block;
      //font-size:1.2rem;
    }
    /deep/ .colpic{
      width: 1.066667rem;
      height: 1.066667rem;
      border-radius: 50%;
      display: inline-block;
      background-color:#daa936;
      background-repeat:no-repeat;
      background-size:80%;
      background-position:center center;
    }
    .jnp{
        background-image:url(../assets/index_pic/jnp.png);
    }
    .hhys{
      background-image:url(../assets/index_pic/hhys.png);
    }
    .wdys{
      background-image:url(../assets/index_pic/wdys.png)
    }
    .jyzb{
      background-image:url(../assets/index_pic/jyzb.png)
    }
    .gwc{
      background-image:url(../assets/index_pic/gwc.png)
    }
}
.newGG{
  border-top:1px solid #e5e5e5;
  border-bottom:1px solid #e5e5e5;
  padding-top:0.266667rem;
  padding-bottom:0.266667rem;
  display: flex;
  .gg_left{
    font-size:0.4rem;
    font-weight:bold;
    width:23.7%;
    text-align: center;
    border-right:1px solid #e5e5e5;
  }
  .gg_left,.gg_right,.gg_right span{
    display: inline-block;
  }
   .gg_left{
    //height:3.2rem;
    line-height:0.986667rem;
 //   float: left;
  }
  .gg_right{
 //   height:3.2rem;
      .laba{ 
        background:url(../assets/laba.png) no-repeat center center;
        //padding:2rem;
        width:1.093333rem;
        height: 0.56rem;
      }
      .ldgg{
       // font-size:1rem;
        line-height: 0.506667rem;
        label{
          background-color:#c79a3b;
          color:#fff;
          border-radius:0.066667rem;
          //font-size:0.8rem;
          padding: 4px 8px;
          margin-right:0.133333rem;
        }
      }

  }
  
}
.rmtj{
  background-color:#f6f6f6;
  height:1.066667rem;
 // line-height:4rem;
 // font-size:1.4rem;
  margin-top:0.4rem;
  padding-left:0.373333rem;
  padding-right:0.373333rem;
  .more_right{
    /* width:1.5rem;
    height:1.066667rem;
    display: inline-block;
    float: right;
    background:url(../assets/right.png) no-repeat center center; */
    color:#c6c6c6;
   // font-size:2rem
  }
}
.rmtjList{
  margin-top:0.4rem;
  padding:0.6rem 0;
  text-align: center;
  span{
    display:block;
   
    //font-size:1.2rem;
  }
  .flag{
    position: absolute;
    top: -1px;
    left: 0.2rem;
    width:10.303%;
  }
}
.spdh_box{
  span{
    display:block;
  }
}
.spdh{
  position:relative;
  padding-top:0.8rem;
  img{
    width:65%;
  }
}
.bottom{
  background-color:#f6f6f6;
  text-align:center;
  padding-top:0.533333rem;
  padding-bottom:0.533333rem;
  .bottom_line{
    width:1.173333rem;
    height:1px;
    display: inline-block;
    vertical-align: middle;
    background-image:url('../assets/botton-line.png');
  
  }
  img{
    vertical-align: middle;
    width:18.8%
  }
}
</style>



