<template>
		<div class="PriceInfo padding8 marginTop-14 backgroundColor-fff font-24" id="priceInfo">
			<div class="borderButton-e5e5e5 padding38">
				<gs name="运费" :value="yf" class="color-888888"></gs>
				<gs name="电子券" :value="Dprice"></gs>
				<gs name="商品总价" :value="priceAll" ></gs>
			</div>
			<div class="padding38">
				<gs name="实付款" :value="priceAll-Math.abs(Dprice)-yf" class="red"></gs>
			</div>
		</div>
</template>
<script>
export default({
	data:function(){
		return {
			yf:'8',
			Dprice:'-10',
			priceAll:'100'
		}
	},
	components:{
		"gs":{
			props:['name','value','vlaueClass'],
			template:'<div class="overflow"><span class="left">{{name}}</span><span class="right price">￥{{value}}</span></div>'
		}
	}
})
</script>
<style lang="less" scoped>
.PriceInfo{
	.red{
	/deep/.price{
			color:#dd2727
		}
	}
}
</style>


