<template>
<div>
		<div class="content padding8 marginTop-14 backgroundColor-fff">
		
			<pan v-for="(item,index) in sp">
<!-- <input type="checkbox" v-model="$store.state.Checked.childChecked[index]" :value="item.price" @click="handleCheck(index)"> -->
				<Checkbox v-show="flag" v-model="$store.state.Checked.childChecked[index]" @click.prevent.native="handleCheck(index)"></Checkbox>
				<!-- <Checkbox v-show="flag" v-model="abc[index]" @click.prevent.native="handleCheck(index)"></Checkbox> -->
					<div class="things_img">
						<img :src="item.img">
					</div>
					<div class="things_introduce">
						<span class="name1 font-24">{{item.name1}}</span>
						<span class="name2 font-24">{{item.name2}}</span>
						<span class="price font-24">￥{{item.price}}</span>
					</div>
				<div class="number">
		    		<buttonCounter change="-" v-on:increment="incrementTotal(index,'sub')" :disabled="attr"></buttonCounter>
		    		<buttonCounter :change="item.num" class="blod" ref="num"></buttonCounter>
		    		<buttonCounter change="+" v-on:increment="incrementTotal(index,'add')" :disabled="attr"></buttonCounter>
		    	</div>
		    </pan>
		</div>
</div>
</template>
<script>

export default({
	props:['Propflag','attr'],
	data:function () {
		return{
			sp:'',
			flag:false,
			single:false
		}
	},
	created:function(){
		let that=this;
		this.$http({
			url:'./static/falseDate/pay.json',
			method:'get'
		}).then(function(data){
			that.sp=data.data.data;
			that.$store.state.Checked.len=that.sp.length;
			for(let i=0;i<that.$store.state.Checked.len;i++){
				//that.$store.state.Checked.childChecked[i]=true;
				that.$set(that.$store.state.Checked.childChecked,i,true)
				that.$parent.total += that.sp[i].price;
			}

		}).catch(function(data){
			console.log(3)
		});
		if(this.Propflag=='checkbox'){
			this.flag=true;
		}
	},

	methods:{
		selectName:function(val){
			console.log(this.child)
		},
		incrementTotal:function(index,type){
			let num=this.$refs.num[index].$el.innerHTML*1;
			if(type=='add'){
				this.$refs.num[index].$el.innerHTML=num+1;
			}else{
				if(num>0)
					this.$refs.num[index].$el.innerHTML=num-1;
			}
		},
		handleCheck:function(index){
			let len=this.$store.state.Checked.len;
			let flag=0;
			this.$parent.total=0.00;
			//this.$store.state.Checked.childChecked[index]=!this.$store.state.Checked.childChecked[index];
			this.$set(this.$store.state.Checked.childChecked,index,!this.$store.state.Checked.childChecked[index])
			for(let i=0;i<len;i++){
				if(!this.$store.state.Checked.childChecked[i]){
					flag=1;
				}else{
					this.$parent.total += this.sp[i].price;
				}
			}
			if(flag==1){
				this.$parent.checked=false;
			}else{
				this.$parent.checked=true;
			}
		}
	},
	components:{
		"pan":{
			template:"<div class='things_pan padding38 flexBetween'><slot></slot></div>"
		},
		"buttonCounter":{
			props:['change'],
			template:'<button type="button" class="buttonCounter backgroundColor-f5f5f5" v-on:click.self.prevent="incrementCounter">{{change}}</button>',
			methods:{
				incrementCounter:function(){
					this.$emit('increment')
				}
			}
		}
	}
})
</script>
<style lang="less" scoped>
a{color:#000}
.addDress{
	.addressText{
		display:inline-block;
		width:95.5%
	}
	.addressMore{
		display:inline-block;
	}
}
.content{
    position: relative;
    .blod{
    	font-weight: bolder;
    }
 	.things_pan:not(:last-child){
 		border-bottom:1px solid #e5e5e5;
 	}
	 .things_img{
		width:26.687%;
		border:1px solid #e5e5e5;
		img{display:block}
    }
    .things_introduce{
    	width:37.391%;
    	span{
    		display:block;
    	}
    	.name1{
    	//	font-size:1.5rem;
    		color:#000;
    		text-overflow:ellipsis;
    		word-break:break-all;
    		display: -webkit-box;
    		-webkit-box-orient: vertical;
    		-webkit-line-clamp: 2;
    		overflow:hidden;

    	}
    	.name2{
    		text-overflow:ellipsis;
    		white-space: nowrap;
    		overflow:hidden;
    		//font-size:1.3rem;
    		color:#666666;

    	}
    	.price{
    		//font-size:1.4rem;
    		line-height:0.64rem;
    		color:#ed0000;
    	}
    }
	.number{
		/* position: absolute;
		    	right: 8px;
		    	top: 39%; */
		width:2.106667rem;
		display: flex;
    	justify-content: space-between;
	}
    .buttonCounter{
    	width: 0.666667rem;
	    height: 0.666667rem;
	    border: none;
    }
}
</style>



