<template>
	<div id="more">
		<topBar name="更多商品"></topBar>
		<div class="search padding8 backgroundColor-fff">
			<Input  icon="ios-search" placeholder="搜索商品" class="color-f6f6f6 marginTop-14 input"></Input>
			<Row class="drop">
		        <Col span="8">
		        	<Dropdown trigger="custom">
				        <a href="javascript:void(0)" class="font-30" @click="handleOpen">
				            商品分类
				            <Icon type="ios-arrow-down"></Icon>
				        </a>
				        <transition name="fade">
					        <Menu ref='spflMenu' v-if="visible">
					            <DropdownItem>纪念品</DropdownItem>
					            <DropdownItem>绘画艺术</DropdownItem>
					            <DropdownItem>舞蹈艺术</DropdownItem>
					            <DropdownItem>剧院周边</DropdownItem>
					        </Menu>
				        </transition>
		    		</Dropdown>
		        </Col>
		        <Col span="8">
		        	<Dropdown trigger="click">
				        <a href="javascript:void(0)" class="font-30">
				            销量排序
				            <Icon type="ios-arrow-down"></Icon>
				        </a>
		    		</Dropdown>
		        </Col>
		        <Col span="8">
					<Dropdown trigger="click">
				        <a href="javascript:void(0)" class="font-30">
				            价格排序
				            <Icon type="ios-arrow-down"></Icon>
				        </a>
		    		</Dropdown>
		       	</Col>
		    </Row>
		</div>
		<div class="content padding8">
			<pan v-for="(item,index) in things">
				<router-link to="/detail" class="flexBetween">
					<div class="things_img">
						<img :src="item.img">
					</div>
					<div class="things_introduce">
						<span class="name1 font-30">{{item.name1}}</span>
						<span class="name2 font-26">{{item.name2}}</span>
						<span class="price font-28">{{item.price}}</span>
					</div>
				</router-link>
			</pan>
			<div id="masker" class="masker" v-if="visible" @click="handleOpen"></div>
		</div>
	</div>
</template>
<script>
export default{
	data:function(){
		return{
			visible: false,
			things:''
		}
	},
	mounted:function(){
		let that=this;
      	this.$http({
        url:'./static/falseDate/more_things.json',
        method:'get',
        data:'',
      }).then(function(data){
        that.things=data.data.data;
      }).catch(function(data){
        console.log(2)
      });

	},
	methods:{
		handleOpen(event){
			this.visible=!this.visible;
		//	this.$refs.spflMenu.$parent.$el.style.cssText='position:fixed';
		//	this.$refs.spflMenu.$parent.$el.style.position='fixed';

		}
	},
	components:{
		"Menu":{
			template:"<div class='Menu font-28'><ul class='backgroundColor-fff'><slot></slot></ul></div>"
		},
		"DropdownItem":{
			template:"<li><slot></slot></li>"
		},
		"pan":{
			template:"<div class='things_pan padding38'><slot></slot></div>"
		}
	}
}
</script>
<style lang="less" scoped>
#more{background-color:#f6f6f6}
a{color:#666666}

/deep/ .input {
	input{
		background-color:#f6f6f6;
		height:0.866667rem;
		font-size: 24px;
	}
	.ivu-icon{
		font-size:30px;
		line-height:0.866667rem;
	}
	
}


.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.drop{
	text-align:center;
	padding: 0.133333rem 0;
	a{
		//font-size:1.5rem;
		line-height:0.8rem;
	}
	.ivu-col:not(:last-child){border-right:1px solid #f6f6f6}
	.Menu{
		width: 100%;
    	position: fixed;
    	left: 0;
    	text-align:left;
    	//font-size:1.4rem;
    	line-height:0.986667rem;
    	text-indent: 0.133333rem;
    	//background-color: #f6f6f6;
    	//padding-top:0.9rem;
    	margin-top: 0.32rem;
    	z-index: 99;
    	/deep/ ul{
	    	padding:0 0.533333rem;
	    	//background-color:#fff;
    	}
	    li{
	    	border-bottom:1px solid #e5e5e5;
	    }
    }  
}

 .content{
 	margin-top: 0.186667rem;
    background-color: #fff;
    position: relative;
    .masker{
		width:100%;
		height:100%;
		background: #000;
	    position: absolute;
	    opacity: 0.2;
	    top:0;
	    left:0;
	}
 	.things_pan{
 		overflow:hidden;
 		border-bottom:1px solid #e5e5e5;
 	}
	 .things_img{
		width:26.687%;
		border:1px solid #e5e5e5;
		img{display:block}
    }
    .things_introduce{
    	width:69.565%;
    	span{
    		display:block;
    	}
    	.name1{
    		//font-size:1.5rem;
    		color:#000;
    		line-height:0.64rem;
    		text-overflow: ellipsis;
    		overflow: hidden;
    		white-space: nowrap;
    	}
    	.name2{
    		//display:box !important;
    		//display: -webkit-box !important;
    		display: -webkit-box;
    		text-overflow:ellipsis;
    		word-break: break-all;
    		display: -webkit-box;
    		-webkit-box-orient: vertical;
    		-webkit-line-clamp: 2;
    		overflow:hidden;
    		color:#666666;
    	}
    	.price{
    		line-height:0.64rem;
    		color:#ed0000;
    	}
    }
}

</style>


