<template>
	<div id="payType">
		<topBar name="付款页面"></topBar>
		<addressBox></addressBox>
		<info></info>
		<priceInfo></priceInfo>
		<payChoose></payChoose>
		<payTime></payTime>
		<!-- <div class="padding8 marginTop-14 backgroundColor-fff font-24 padding26 color-888888">
			<p>订单编号:  {{DDNo}}</p>
			<p>创建时间:  {{CJdate}}{{CJtime}}</p>
			<p>支付时间： {{Paydata}}{{Paytime}}</p>
		</div> -->
		<div class="padding8 marginTop-14 backgroundColor-fff font-24 padding26 borderTop-e5e5e5 align">
			<button type="button" class="color-ff0000 border-ff0000 backgroundColor-fff font-28 button1" @click.prevent="pay">立即支付</button>
		</div>
	</div>
</template>
<script>
import info from './info.vue'
import addressBox from './addressBox.vue'
import priceInfo from './priceInfo.vue'
import payChoose from './payChoose.vue'
import payTime from './payTime.vue'
export default({
	data:function () {
		return{
			
			/*DDNo: 2257412700164073,
			CJdate:"2016-10-10",
			CJtime:"10:25:09",
			Paydata:"2016-10-10",
			Paytime:"10:25:09"*/

		}
	},
	created:function(){
	},
	methods:{
		pay:function(){
			this.$router.push({path:"/wait_status"})
		}
	},
	components:{
		addressBox,
		info,
		priceInfo,
		payChoose,
		payTime
		// "gs":{
		// 	props:['name','value','vlaueClass'],
		// 	template:'<div class="overflow"><span class="left">{{name}}</span><span class="right price">￥{{value}}</span></div>'
		// },
		// "payType":{
		// 	template:'<div class="overflow payTypeBox"><span class="PayIcon left"></span><span class="right"><slot></slot></span></div>'
		// }
	}
})
</script>
<style lang="less" scoped>
#payType{background-color:#f6f6f6}
.PriceInfo{
	.red{
	/deep/.price{
			color:#dd2727
		}
	}
}
.payType{
	/deep/.ivu-checkbox-inner{
		width:0.32rem;
		height:0.32rem
	}
	/deep/ .ivu-checkbox-checked .ivu-checkbox-inner:after{
		width:0.133333rem;
		height:0.24rem
	}
}

</style>


