<template>
	<div id="detail">
		<topBar name="商品详情" ref="topBar"></topBar>
		<div class="top">
			<v-touch @swipeleft="onSwipeLeft" @swiperight="onSwiperight" class="touch backgroundColor-fff">
				<Carousel v-model="value1" loop :dots="setting.dots" :radius-dot="setting.radiusDot" :arrow="setting.arrow">
			        <CarouselItem>
			            <div class="demo-carousel"><img :src="spxq"></div>
			        </CarouselItem>
			        <CarouselItem>
			            <div class="demo-carousel"><img :src="spxq"></div>
			        </CarouselItem>
			    </Carousel>
		    </v-touch>
		    <div class="padding8 backgroundColor-fff">
		    	<p class="font-24">雅轩斋红木雕刻实木大象摆件 风水木象摆件大号 花梨 木质 配对红木雕刻实木大象摆件 风水木象摆件大号花梨木质雕刻好实木大大象摆件 风水木象摆件大号花梨木质</p>
		    	<p class="color-e63f18 font-24 padding26 jfBox"><span class="jf"></span>6000</p>
		    	<div class="padding24 borderButton-e5e5e5"><span class="font-24">已选</span><span class="choosed_text">{{choosedText}}</span><span style="float:right">{{num}}件</span></div>
		    	<div class="padding24 borderButton-e5e5e5 flex">
		    		<span class="font-24">类型</span>
		    		<Row :gutter="6" class="button_row font-22">
		    			<Col span="6" v-for="(item,index) in button">
		    				<Button :class="{choosed:item.choosed}" class="font-22" :value="item.name" @click="choosed(item.name)">{{item.name}}</Button>
		    			</Col>
		    		</Row>
		    	</div>
		    	<div class="padding24 flexBetween">
		    		<span class="font-24">数量</span>
		    		<span>
		    			<buttonCounter @click.native="sub">-</buttonCounter>
		    			<buttonCounter><b>{{num}}</b></buttonCounter>
		    			<buttonCounter @click.native="sum">+</buttonCounter>
		    			<span>库存{{Allnum}}件</span>
		    		</span>
		    	</div>
		    </div>
	    </div>
	    <div class="yf padding8 backgroundColor-fff marginTop-20">
	    	<div class="padding24 borderButton-e5e5e5">
	    		<span class="font-24">运费</span>
	    		<span>{{pricePrompt}}</span>
	    	</div>
	    	<div class="padding24 flex">
	    		<span style="margin-right: 3px;" class="font-24">提示</span>
	    		<ul class="Prompt">
	    			<li v-for="item in Prompt">{{item}}</li>
	    		</ul>
	    	</div>
	    </div>
	 <!--   <v-touch @swipeup=onSwipeUp> -->
			<div class="pl padding8 backgroundColor-fff marginTop-20">
				<div class="padding24 font-24">
					商品评价 ({{pjNum}})
				</div>
				<div v-for="(item,index) in sppj" v-if="index<plNum" class="borderTop-e5e5e5 padding24">
					<div class="font-22">{{item.name}}</div>
					<div class="font-24 line-height-42">{{item.content}}</div>
					<div class="color-666666 font-20">
						<span>{{item.time}}</span>
						<span>{{item.type}}</span>
					</div>
					<div v-if="item.img.length!=0" class="marginTop-14">
						<ul class="img">
							<li v-for="(img,index) in item.img">
								<img :src="img">
							</li>
						</ul>
					</div>
				</div>
			    <div class="align font-24 line-height-60" v-if="sppj.length>=plNum" @click="viewOther">查看所有评论<Icon type="ios-arrow-down" style="margin-left:2px"></Icon></div>
			</div>
		
		<!-- 	<div class="font-24 padding38 align">继续拖动，查看图文详情</div> -->
		<!-- </v-touch> -->
		<div class="content" ref="content"></div>
		<transition :duration="{ enter: 5000}">
		<div class="contentInfo padding8 backgroundColor-fff" v-show="top">
		    <ul class="padding24">
		    	<tab class="font-24" :class="{colorDd2727:show}"  @click.native="tab('one')" index="one">图文详情</tab>
		    	<tab class="font-24" :class="{colorDd2727:!show}" @click.native="tab('two')" index="two">商品损坏</tab>
		    </ul>
			<div ref="one">
				<img v-for="item in bigImg" :src="item" v-show="show">
			</div>
			<div ref="two" class="font-24" v-show="!show">
				<p class="borderButton-e5e5e5 padding24">1、折扣：如无特殊说明，折扣指销售商在原价、或划线价（如品牌专柜标价、商品吊牌价、厂商指导价、厂商建议零售价）等某一价格基础上计算出的优惠比例或优惠金额；如有疑问，您可在购买前联系江苏大剧院进行咨询。</p>
				<p class="borderButton-e5e5e5 padding24">2、异常问题：商品促销信息以商品详情页“促销”栏中的信息为准；商品的具体售价以订单结算页价格为准；如您发现活动商
品售价或促销信息有异常，建议购买前先联系江苏大剧院进行咨询。</p>
				<p class="padding24">3、苏大剧院维权电话：<span class="color-dd2727">520-88888888</span></p>
			</div>
		   
		</div>
		</transition>
		<space></space>
		<div class="footer backgroundColor-fff borderTop-e5e5e5">
			<div class="heart font-24 color-666666" :class="{heartChoosed:heartType}" @click="heart">收藏</div>
			<div class="button">
					<a class="width gwc font-30" @click="add">加入购物车</a>
				<router-link to="/pay">
					<a class="width gm font-30">立即购买</a>
				</router-link>
			</div>
		</div>
	</div>
</template>
<script>
export default({
	data:function(){
		return{
			value1:0,
			spxq:'./static/images/spxq.png',
			button:'',
			choosedText:'',
			num:1,
			Allnum:'',
			pricePrompt:'',
			Prompt:'',
			pjNum:'',
			sppj:'',
			plNum:5,
			bigImg:'',
			show:true,
			heartType:false,
			top:true,
			startX:'',
			startY:'',
			setting:{
				dots:'inside',
				radiusDot: true,
				arrow:'never'
			}
		}
	},
	created:function(){
		let that=this;
		this.$http({
			url:'./static/falseDate/chooseButton.json',
			method:'get',
			data:''
		}).then(function(data){
			that.button=data.data.data;
			that.Allnum=data.data.num.num;
			that.button.forEach(function(item,index){
				if(item.choosed==true){that.choosedText=item.name}
			})
			
		}).catch(function(data){
			console.log(3)
		});
		this.$http({
			url:'./static/falseDate/yunshu.json',
			method:'get'
		}).then(function(data){
			that.pricePrompt=data.data.pricePrompt;
			that.Prompt=data.data.Prompt;
			
		}).catch(function(data){
			console.log(2)
		});
		this.$http({
			url:'./static/falseDate/sppj.json',
			method:'get'
		}).then(function(data){
			that.sppj=data.data.data;
			that.pjNum=data.data.pjnum;
			that.bigImg=data.data.bigImg;

		}).catch(function(){
			console.log(2);
		})

		
	},
	mounted:function(){
		//window.addEventListener('touchstart',this.touchstart)
		//window.addEventListener('touchmove', this.handleScroll)
		
		this.$nextTick(function(){
			
               /* alert('v-for渲染已经完成')*/

            })
	},

	methods:{
		touchstart:function(e){
			//console.log(e)
			//e.preventDefault();
			/*e.originalEvent.changedTouches[0].pageX */
			this.e=  e.changedTouches[0].pageX ;
			this.startX = e.changedTouches[0].pageX,
　　　　	this.startY = e.changedTouches[0].pageY;

		},
		handleScroll:function(e){
			//e.preventDefault();
			let moveEndX = e.changedTouches[0].pageX;
	　　　　let moveEndY = e.changedTouches[0].pageY;
	　　　　let X = moveEndX - this.startX;
	　　　　let Y = moveEndY - this.startY;
			this.X=X;
			this.Y=Y;
		//	let Y=-1;
			if(Y<0){
				let scrollTop= document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
				let scrollContent=this.$refs.content.offsetTop;
			/*if(document.documentElement.clientHeight+scrollTop>=document.body.clientHeight){

				document.documentElement.scrollTop=scrollContent;
				this.top=true;

			}*/
				/*this.one=document.documentElement.clientHeight;
				this.two=scrollTop;
				this.three=scrollContent;*/
				this.scrollContent=scrollContent;
				if(document.documentElement.clientHeight+scrollTop-10>=scrollContent){
					this.top=true;
					document.documentElement.scrollTop=scrollContent;
					window.pageYOffset=scrollContent;
					document.body.scrollTop=scrollContent;
				/*	if(document.documentElement.scrollTop ){
						this.one='1';
						document.documentElement.scrollTop=scrollContent;
						return;
					}else if(window.pageYOffset){
						this.two='2';
						window.pageYOffset=scrollContent;
						return;
					}else{
						this.three='3';
						document.body.scrollTop=scrollContent;
						return;
					}*/
					
				}
			}
			

		},
		onSwipeLeft(){
			document.querySelectorAll(".touch .left")[0].click();
		},
		onSwiperight(){
			document.querySelectorAll(".touch .right")[0].click();
		},
		// onSwipeUp(){
		// 	let scrollTop=document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
		// 	let scrollContent=this.$refs.content.offsetTop;
		// 	//console.log(scrollContent)
		// 	/*if(document.documentElement.clientHeight+document.documentElement.scrollTop>=document.body.clientHeight){
		// 		alert('up2')
		// 		this.top=true;
		// 	}*/
		// 	this.one=document.documentElement.clientHeight;
		// 	this.two=scrollTop;
		// 	this.three=scrollContent;
			
		// 	if(document.documentElement.clientHeight+scrollTop>=scrollContent){
		// 		this.top=true;
		// 		if(document.documentElement.scrollTop ){
		// 			document.documentElement.scrollTop=scrollContent;
		// 			return;
		// 		}else if(window.pageYOffset){
		// 			window.pageYOffset=scrollContent;
		// 			return;
		// 		}else{
		// 			document.body.scrollTop=scrollContent;
		// 			return;
		// 		}
				
		// 	}
			
		// },
		choosed:function(val){
			document.querySelectorAll(".button_row .choosed")[0].classList.remove('choosed');
			if(event.target.tagName=='SPAN'){
				event.target.parentNode.classList.add("choosed")
			}else{
				event.target.classList.add("choosed")
			}
			this.choosedText=val;
		},
		sum: function(){
			this.num=this.num+1;
		},
		sub: function(){
			if(this.num>1)
				this.num=this.num-1;
		},
		viewOther:function(){
			this.plNum=this.plNum+5;
		},
		tab:function(index){
			if(index=='one'){
				this.show=true;
				/*this.$refs.two.style.display="none";
				this.$refs.one.style.display="block";*/
				
			}else{
				this.show=false;
				this.$refs.two.style.height=this.$refs.one.scrollHeight+'px';
			/*	let scrollTop=document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
				let scrollContent=this.$refs.content.offsetTop;

				if(document.documentElement.clientHeight+scrollTop>=scrollContent){
					if(document.documentElement.scrollTop ){
						document.documentElement.scrollTop=scrollContent;
						return;
					}else if(window.pageYOffset){
						window.pageYOffset=scrollContent;
						return;
					}else{
						document.body.scrollTop=scrollContent;
						return;
					}
				}*/
			}
		},
		heart:function(){
			this.heartType=!this.heartType;
		},
		add:function(){
			//alert('添加成功')
			this.$Message.success('添加成功');
		}
	},
	components:{
		"buttonCounter":{
			template:'<button type="button" class="buttonCounter backgroundColor-f5f5f5"><slot></slot></button>'
		},
		"tab":{
			props:['index'],
			template:'<li @click="b"><slot></slot></li>',
			methods:{
				b:function(){
					//let index=index
					//console.log(this.index)
					
				}
			}
		}
	}
})
</script>
<style lang="less" scoped>
#detail{background-color:#f6f6f6;}

/deep/	.ivu-carousel-dots li button{
		background: #f90;
	}
.top{
	.jfBox{
		display: flex;
		.jf{
			background:url(../../assets/jf.png) no-repeat center center;
			width: 0.4rem;
		    height: 0.4rem;
		    display: inline-block;
		    background-size: 100%;
		    margin-right: 0.133333rem;
		}
	}
	.button_row{
		margin-left:0.4rem  !important;
	}
	.ivu-btn{
		border: none;
		background-color:#f5f5f5;
	//	margin-left:0.9rem;
		padding: 0.266667rem 0.213333rem;
	}
	.choosed{
		background-color:#c79a3b;
		color:#fff;
	}
	.choosed_text{color:#c79a3b;margin-left: 0.4rem}
	.buttonCounter{
		width:0.693333rem;
		height:0.693333rem;
		border: none;
	}
}
.yf{
	.Prompt{display: inline-block;}
	.Prompt li:before{
		content:'●';
		color:#d18d00;
	}
	[data-dpr="1"] .Prompt li:before{
		font-size:0.133333rem
	}
	[data-dpr="2"] .Prompt li:before{
		font-size:0.266667rem
	}
	[data-dpr="3"] .Prompt li:before{
		font-size:0.4rem
	}
}
.pl{
	.img{
		white-space: nowrap;
		overflow-x: scroll;
		-webkit-overflow-scrolling:touch; 
		li{
			width:25%;
			display:inline-block;
			margin-right:0.08rem;
			img{
				//display:block
			}
		}
	}
}
.contentInfo{
	ul{
		font-size:0;
			li{
			display:inline-block;
			width:50%;
			text-align:center;
		}
	}
}
.footer{
	overflow:hidden;
	position: fixed;
    bottom: 0;
    width: 100%;
	.heart{
		width:1.466667rem;
		height:1.306667rem;
		background-image:url(../../assets/heart.png);
		background-repeat: no-repeat;
    	background-size: 36.363%;
    	background-position: center 0.226667rem;
    	float: left;
    	padding-top:0.8rem;
    	text-align: center;
    	border-right:1px solid #e5e5e5

	}
	.heartChoosed{
		background-image:url(../../assets/heartChoosed.png);
		color:#dd2727;
	}
	.width{
		width:2.933333rem;
		height:1.306667rem;
		line-height:1.306667rem;
		display:block;
    	display: inline-block;
    	text-align:center;
    	color:#fff;
	}
	.gwc{
		background-color:#ff9402;
		float:left;
	}
	.gm{
		background-color:#dd2727
	}
	.button{
		float:right;
		overflow:hidden;
	}
}
</style>


