import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)



import Index from '@/components/index';//首页
import more from '@/components/more/more';//更多商品
import detail from '@/components/thing_introduce/details';//商品详情
import pay from '@/components/003/pay.vue';//立即支付
import payType from '@/components/003/payType.vue';//支付方式
import address from '@/components/003/address.vue';
import addAddress from '@/components/003/addAddress.vue';//增加地址
import card from '@/components/003/card.vue';//卡券
import cardInfo from '@/components/003/cardInfo.vue';//卡券详情
import shoppingCard from '@/components/004/Shopping Cart.vue';//购物车
import wait_status from '@/components/004/wait_status.vue';//待发货


export default new Router({
  routes: [
    {
      path: '/home',
      name: 'index',
      component: Index
    },
    {
    	path:'/more',
    	name:'more',
    	component:more
    },
    {
    	path:'/detail',
    	name:'detail',
    	component:detail
    },
    {
    	path:'/pay',
    	name:'pay',
    	component:pay
    },
    {
    	path:'/payType',
    	name:'payType',
    	component:payType
    },
    {
    	path:'/address',
    	name:'address',
    	component:address
    },
    {
    	path:'/addAddress',
    	name:'addAddress',
    	component:addAddress
    },
    {
    	path:'/card',
    	name:'card',
    	component:card
    },
    {
    	path:'/cardInfo',
    	name:'cardInfo',
    	component:cardInfo
    },
    {
    	path:'/shoppingCard',
    	name:'shoppingCard',
    	component:shoppingCard
    },
    {
    	path:'/wait_status',
    	name:'wait_status',
    	component:wait_status
    },
    {path:'/',redirect:'/home'}

  ]
})



