<template>
	<div id="wait_status">
		<topBar name="订单详情"></topBar>
		<status name="wait"></status>
		<addressBox attr="disabled"></addressBox>
		<info attr="disabled"></info>
		<priceInfo></priceInfo>
		<div class="payType padding8 marginTop-14 backgroundColor-fff font-24 flexBetween">
			<span>支付方式</span>
			<div class="PayIcon" :class="type"></div>
		</div>
		<payTime></payTime>
		<div class="return padding8 marginTop-14 backgroundColor-fff borderTop-e5e5e5 borderButton-e5e5e5">
			<Button type="ghost" class="font-24">退款</Button>
		</div>
		<space></space>
	</div>
</template>
<script>
import status from './status.vue'
import addressBox from './../003/addressBox.vue'
import info from './../003/info.vue'
import priceInfo from './../003/priceInfo.vue'
import payTime from './../003/payTime.vue'

export default({
	data:function(){
		return{
			type:'Alipay'
		}
	},
	components:{
		status,
		addressBox,
		info,
		priceInfo,
		payTime
		
	}
})
</script>
<style lang="less" scoped>
.payType{
	height:1.333333rem;
	.PayIcon{
		width:0.533333rem;
		height:0.533333rem;
		
	}
	.PayIcon{
		background:url(../../assets/Alipay.png) no-repeat center center;
		background-size: cover;
	}
	.WeChat .PayIcon{
		background:url(../../assets/WeChat.png) no-repeat center center;
		background-size: cover;
	}
	.baidu .PayIcon{
		background:url(../../assets/baidu.png) no-repeat center center;
		
	}
}
.return{
	height:1.333333rem;
	line-height: 1.333333rem;
	text-align:right;
	.button{
		width:1.946667rem;
		height:0.8rem
	}
}
</style>


