<template>
	<div id="status" class="status flex">
		<div class="info_txt marginLeft-60">
			<p class="font-36">付款成功</p>
			<p class="font-24">您的包裹正整装待发</p>
		</div>
		<div class="info_img marginRight-60">
			<img :src="img_src">
		</div>
	</div>
</template>
<script>
export	default({
	props:['name'],
	data:function(){
		return{
			img_src:''
		}
	},
	created:function(){
		if(this.name=='wait'){
			this.img_src='./static/images/status/wait.png';

		}
	}
})
</script>
<style lang="less" scoped>
	.status{
		height:2.666667rem;
		background-color:#f44336;
		position:relative;
		.info_txt{
			color:#fff;
			width:56.267%
		}
		.info_img{
			width:43.733%;
			position:absolute;
			bottom:0;
			right:0
		}

	}
</style>


